import React, { useState, useEffect } from 'react';
import { Line } from 'react-chartjs-2';
import { Chart as ChartJS, CategoryScale, LinearScale, PointElement, LineElement, Title, Tooltip, Legend } from 'chart.js';
import { useSearchParams } from 'react-router-dom';

ChartJS.register(CategoryScale, LinearScale, PointElement, LineElement, Title, Tooltip, Legend);

function BMICalculator() {
    const [weight, setWeight] = useState("");
    const [height, setHeight] = useState("");
    const [bmi, setBmi] = useState(null);
    const [weightHistory, setWeightHistory] = useState([]);
    const [searchParams] = useSearchParams();
    const [actualUserId, setActualUserId] = useState(null);
    const [noWeightMessage, setNoWeightMessage] = useState(false);
    const [showGraph, setShowGraph] = useState(true); //    

    const userId = searchParams.get('userId') || 'default_name';

    useEffect(() => {
        setActualUserId(userId);  // URL userId 
        console.log('URL  userId :', userId);
    }, [userId]);

    useEffect(() => {
        if (actualUserId) {
            console.log("userId actualUserId  .");
            updateWeightHistory();  //   
        }
    }, [actualUserId]);

    useEffect(() => {
        if (weightHistory.length === 0) {
            setNoWeightMessage(true);  //    
        } else {
            setNoWeightMessage(false);
        }
    }, [weightHistory]);

    const calculateBMI = async () => {
        if (!weight || !height) {
            setNoWeightMessage(true);  //    
            console.error('   !');
            return;
        }

        const heightInMeters = height / 100;
        const bmiValue = weight / (heightInMeters * heightInMeters);
        setBmi(parseFloat(bmiValue.toFixed(2)));

        const today = new Date();
        today.setHours(today.getHours() + 9);  //   
        const formattedDate = today.toISOString().slice(0, 19).replace('T', ' ');

        try {
            if (!actualUserId) {
                console.error('User ID is missing!');
                return;
            }

            const response = await fetch(`http://223.194.154.149:5001/api/bmi`, {
                method: "POST",
                headers: { "Content-Type": "application/json" },
                body: JSON.stringify({
                    date: formattedDate,
                    weight: weight,
                    userId: actualUserId,
                }),
            });

            const data = await response.json();

            if (data.success) {
                console.log("   !");
                updateWeightHistory();
                setWeight("");  //  
                setBmi(null);    // BMI  
            } else {
                console.error("   :", data.message);
            }
        } catch (error) {
            console.error("     :", error);
        }
    };

    const updateWeightHistory = async () => {
        try {
            const response = await fetch(`http://223.194.154.149:5001/api/bmi?userId=${actualUserId}`);
            if (!response.ok) {
                throw new Error('   ');
            }
            const data = await response.json();
            const formattedData = data.map(entry => {
                const utcTime = new Date(entry.date);  // UTC  
                const korTime = new Date(utcTime);
                korTime.setHours(korTime.getHours() + 9); //   

                return {
                    ...entry,
                    date: korTime.toISOString(),
                };
            });
            setWeightHistory(formattedData);
        } catch (error) {
            console.error("   :", error);
        }
    };

    const handleDeleteWeight = async (record) => {
        const { date, weight } = record;
        try {
            const formattedDate = new Date(date);
            formattedDate.setHours(formattedDate.getHours());  //   

            const deleteDate = formattedDate.toISOString().slice(0, 19).replace('T', ' ');

            const response = await fetch(`http://223.194.154.149:5001/api/bmi`, {
                method: "DELETE",
                headers: { "Content-Type": "application/json" },
                body: JSON.stringify({
                    date: deleteDate,
                    weight: weight,
                    userId: actualUserId,
                }),
            });

            if (response.ok) {
                console.log("   !");
                const updatedHistory = weightHistory.filter(item => item.date !== record.date);
                setWeightHistory(updatedHistory);
            } else {
                console.error("   ");
            }
        } catch (error) {
            console.error("     :", error);
        }
    };

    const handleAddWeight = async () => {
        if (weight && height) {
            calculateBMI();
            setWeight("");  //  
        } else {
            alert("   .");
        }
    };

    const chartData = {
        labels: weightHistory.map(entry => entry.date),
        datasets: [
            {
                label: '  ',
                data: weightHistory.map(entry => entry.weight),
                fill: false,
                borderColor: 'blue',
                tension: 0.1,
            },
        ],
    };

    return (
        <div style={{ padding: "20px" }}>
            <h2>BMI </h2>
            <h3>: {actualUserId}</h3>  {/*  ID   */}
            <input
                type="number"
                placeholder=" (kg)"
                value={weight}
                onChange={(e) => setWeight(e.target.value)}
                style={{ marginRight: "10px" }}
            />
            <input
                type="number"
                placeholder=" (cm)"
                value={height}
                onChange={(e) => setHeight(e.target.value)}
                style={{ marginRight: "10px" }}
            />
            <button onClick={() => { console.log("Button clicked!"); calculateBMI(); }}></button>
            {bmi && <p> BMI: {bmi}</p>}

            {/*      */}
            <div style={{ marginTop: "20px" }}>
                <button onClick={() => setShowGraph(!showGraph)}>
                    {showGraph ? "  " : "   "}
                </button>
            </div>

            {/*        */}
            {showGraph ? (
                <div style={{ width: "80%", marginTop: "30px" }}>
                    <Line data={chartData} />
                </div>
            ) : (
                <div>
                    <h3> </h3>
                    <ul>
                        {weightHistory.map(record => (
                            <li key={record.date}>
                                {record.date} - {record.weight}kg{" "}
                                <button onClick={() => handleDeleteWeight(record)}></button>
                            </li>
                        ))}
                    </ul>
                    {noWeightMessage && <p>   .</p>}
                </div>
            )}
        </div>
    );
}

export default BMICalculator;
