import '@fortawesome/fontawesome-free/css/all.min.css';
import React, { useEffect, useState } from 'react';
import { BrowserRouter as Router, Route, Routes, Link } from 'react-router-dom';
import FitnessEquipInfo from './pages/FitnessEquipInfo';
import FitnessPlan from './pages/FitnessPlan';
import BMICalculator from './pages/BMICalculator';
import Header from './components/Header';
import './App.css'; //   

function App() {
    const [userId, setUserId] = useState('default_name');  //  'default_name' 

    useEffect(() => {
        //    userId  
        const storedUserId = localStorage.getItem('userId'); //     
        if (storedUserId) {
            setUserId(storedUserId); //    userId   
        }
    }, []);

    return (
        <Router>
            <div style={{ textAlign: 'center' }}>
                <Header />
                <nav className="nav-container">
                    <Link to="/info" className="nav-button2"> </Link>
                    <Link to={`/plan?userId=${userId}`} className="nav-button2">  </Link>
                    <Link to={`/bmi?userId=${userId}`} className="nav-button2">BMI </Link>
                </nav>
                <Routes>
                    {/*   */}
                    <Route path="/info" element={<FitnessEquipInfo />} />
                    
                    {/*     */}
                    <Route path="/info/:equipmentName" element={<FitnessEquipInfo />} />
                    
                    {/*     */}
                    <Route path="/plan" element={<FitnessPlan />} />
                    
                    {/*    */}
                    <Route path="/bmi" element={<BMICalculator />} />
                </Routes>
            </div>
        </Router>
    );
}

export default App;
